import React, { useState, useEffect, useRef } from "react";
import { MapContainer, TileLayer, Marker, Popup, useMap } from "react-leaflet";
import "leaflet/dist/leaflet.css";
import L from "leaflet";
import "leaflet-routing-machine";
import "leaflet-control-geocoder";
import { FaMapMarkerAlt } from "react-icons/fa";
import ReactDOMServer from "react-dom/server";
import "./App.css";
import { MDBAccordion, MDBAccordionItem, MDBIcon, MDBListGroup, MDBListGroupItem } from "mdb-react-ui-kit";
import ModalComponent from "./ModalComponent"; // Import the modal component

const mapContainerStyle = {
  width: "100%",
  height: "80vh",
  position: "relative",
  zIndex: -1, // Lower than modal
};

const inputContainerStyle = {
  position: "absolute",
  top: "20px",
  left: "20px",
  zIndex: 1000,
  backgroundColor: "rgba(255, 255, 255, 0.8)",
  padding: "10px",
  marginLeft:'50px',
  borderRadius: "8px",
  boxShadow: "0px 2px 10px rgba(0, 0, 0, 0.1)",
};

const RoutingMachine = ({ start, end }) => {
  const map = useMap();
  const routingControlRef = useRef(null);

  useEffect(() => {
    if (!map || !start || !end) return;

    if (routingControlRef.current) {
      map.removeControl(routingControlRef.current);
    }

    routingControlRef.current = L.Routing.control({
      waypoints: [L.latLng(start.lat, start.lng), L.latLng(end.lat, end.lng)],
      routeWhileDragging: true,
      geocoder: L.Control.Geocoder.nominatim(),
      lineOptions: {
        styles: [{ color: "blue", weight: 4 }],
      },
    }).addTo(map);

    return () => {
      if (routingControlRef.current) {
        map.removeControl(routingControlRef.current);
      }
    };
  }, [map, start, end]);

  return null;
};

const CustomMarker = ({ position, icon, onClick }) => (
  <Marker position={position} icon={icon} eventHandlers={{ click: onClick }}>
    <Popup>{`Lat: ${position.lat}, Lng: ${position.lng}`}</Popup>
  </Marker>
);

function App() {
  const [active, setActive] = useState(1);
  const [start, setStart] = useState(null);
  const [end, setEnd] = useState(null);
  const [destinationInput, setDestinationInput] = useState("");
  const [isSatelliteView, setIsSatelliteView] = useState(false);
  const [destinations, setDestinations] = useState([]);
  const [modalOpen, setModalOpen] = useState(false);
  const [currentLocation, setCurrentLocation] = useState(null);

  useEffect(() => {
    navigator.geolocation.getCurrentPosition(
      (position) => {
        setStart({
          lat: position.coords.latitude,
          lng: position.coords.longitude,
        });
      },
      (error) => {
        console.error("Error fetching current location:", error);
        setStart({ lat: 51.505, lng: -0.09 });
      }
    );
  }, []);

  const geocode = (address, setLocation) => {
    const geocoder = L.Control.Geocoder.nominatim();
    geocoder.geocode(address, (results) => {
      if (results && results.length > 0) {
        const { center, name, properties } = results[0];
        const locationName = properties?.name || name || address;
        const date = new Date();
        const formattedDate = date.toLocaleDateString();
        const formattedTime = date.toLocaleTimeString();

        setLocation({ lat: center.lat, lng: center.lng });

        setDestinations((prevDestinations) => [
          ...prevDestinations,
          {
            lat: center.lat,
            lng: center.lng,
            locationName,
            date: formattedDate,
            time: formattedTime,
          },
        ]);
      } else {
        alert("Location not found");
      }
    });
  };

  const handleCalculateRoute = () => {
    if (destinationInput) {
      geocode(destinationInput, setEnd);
    }
  };

  const handleHistoryClick = (destination) => {
    setEnd({ lat: destination.lat, lng: destination.lng });
  };

  const handleMarkerClick = (location) => {
    setCurrentLocation(location);
    setModalOpen(true);
  };

  const startIcon = L.divIcon({
    className: "custom-icon",
    html: `<div style=" border-radius: 50%; padding: 10px; color: white; font-size: 20px;">${ReactDOMServer.renderToStaticMarkup(
     
      <MDBIcon fas icon="car-side" style={{ color: "blue" }}/>
    )}</div>`,
  });

  const endIcon = L.divIcon({
    className: "custom-icon",
    html: `<div style=" border-radius: 50%; padding: 10px; color: white; font-size: 20px;">${ReactDOMServer.renderToStaticMarkup(
      <FaMapMarkerAlt style={{ color: "red" }} />
    )}</div>`,
  });

  return (
    <div>
      <div style={mapContainerStyle}>
        <div style={inputContainerStyle}>
          <input
            type="text"
            value={destinationInput}
            placeholder="Enter destination"
            onChange={(e) => setDestinationInput(e.target.value)}
            style={{ marginRight: "10px", padding: "5px", width: "200px" }}
          />
          <button
            onClick={handleCalculateRoute}
            style={{
              padding: "6px 12px",
              backgroundColor: "#007bff",
              color: "#fff",
              border: "none",
              borderRadius: "5px",
              cursor: "pointer",
            }}
          >
            Calculate Route
          </button>

          <button
            onClick={() => setIsSatelliteView(!isSatelliteView)}
            style={{
              padding: "6px 12px",
              backgroundColor: "#28a745",
              color: "#fff",
              border: "none",
              borderRadius: "5px",
              cursor: "pointer",
              marginTop: "10px",
            }}
          >
            {isSatelliteView ? "Switch to Terrain View" : "Switch to Satellite View"}
          </button>
        </div>

        {start && (
          <MapContainer
            style={mapContainerStyle}
            center={[start.lat, start.lng]}
            zoom={13}
          >
            {isSatelliteView ? (
              <TileLayer
                url="https://{s}.tile.openstreetmap.fr/hot/{z}/{x}/{y}.png"
                attribution='&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
              />
            ) : (
              <TileLayer
                url="https://{s}.tile.opentopomap.org/{z}/{x}/{y}.png"
                attribution='&copy; <a href="https://www.opentopomap.org/copyright">OpenTopoMap</a> contributors'
              />
            )}

            {start && <CustomMarker position={start} icon={startIcon} />}
            {end && <CustomMarker position={end} icon={endIcon} />}
            {start && end && <RoutingMachine start={start} end={end} />}
          </MapContainer>
        )}
      </div>

      <div style={{ marginTop: "20px", padding: "10px" }}>
        <h3>Previous Destinations</h3>
        <ul>
          {destinations.map((destination, index) => (
            <li key={index} onClick={() => handleHistoryClick(destination)} style={{ cursor: "pointer" }}>
              <strong>Location:</strong> {destination.locationName || "Unknown"} <br />
              <strong>Lat:</strong> {destination.lat}, <strong>Lng:</strong> {destination.lng} <br />
              <strong>Date:</strong> {destination.date} <br />
              <strong>Time:</strong> {destination.time}
            </li>
          ))}
        </ul>
        <MDBAccordion active={active} onChange={(itemId) => setActive(itemId)}>
          <MDBAccordionItem collapseId={1} headerTitle='Accordion Item #1'>
            <MDBListGroup light numbered style={{ minWidth: '22rem' }}>
              <ul>
                <MDBListGroupItem>
                  {destinations.map((destination, index) => (
                    <li key={index} onClick={() => handleHistoryClick(destination)} style={{ cursor: "pointer" }}>
                      <strong>Location:</strong> {destination.locationName || "Unknown"} <br />
                      <strong>Lat:</strong> {destination.lat}, <strong>Lng:</strong> {destination.lng} <br />
                      <strong>Date:</strong> {destination.date} <br />
                      <strong>Time:</strong> {destination.time}
                    </li>
                  ))}
                </MDBListGroupItem>
              </ul>
              <MDBListGroupItem>Cras justo odio</MDBListGroupItem>
              <MDBListGroupItem>Cras justo odio</MDBListGroupItem>
            </MDBListGroup>
          </MDBAccordionItem>
        </MDBAccordion>
      </div>

      {/* Modal for current location details */}
      <ModalComponent
        isOpen={modalOpen}
        onRequestClose={() => setModalOpen(false)}
        location={currentLocation}
      />
    </div>
  );
}

export default App;
